* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: rgba(0, 0, 0, 0.885);
}

.container {
    margin: 50px auto;
    display: flex;
    justify-content: center;
}

.background {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 600px;
    background-color: black;
    border-radius: 20px;
}

.displayWrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    margin-bottom: 20px;
    background-color: rgb(117, 117, 117);
    border-radius: 15px;
    padding: 10px;
    font-size: 30px;
    color: white;
    height: 180px;
    width: 90%;
}

.buttons {
    padding: 0 15px 10px;
    display: flex;
    flex-direction: row;
    height: 380px;
    width: 90%;
}

.leftGroup {
    display: flex;
    flex-direction: column;
    width: 75%;
}

.earse {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 0 6px;
}

.numbers {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    padding: 6px;
    height: 80%;
}

.zero {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}



.operators {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 25%;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 65px;
    width: 65px;
    color: white;
    font-size: 30px;
    border-radius: 50%;
    cursor: pointer;
}

.gray {
    background-color: rgb(73, 73, 73);
}

.lightGray {
    background-color: rgb(153, 153, 153);
}

.orange {
    background-color: orange;
}